html {
  height: 100%;
}

body {
  height: 100%;
  margin: 0;
  /* background-size: 100%; */
}

div#root {
  height: 100%;
}

.bg {
  height: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: 100% 100%;
}

img.btn {
  width: 300px;
  height: 50px;
}
img.small-btn {
  width: 200px;
  height: 30px;
}

.back-btn {
  position: absolute;
  bottom: 2px;
  left: 2px;
  width: 50px;
  height: 50px;
}

